
































































import { Component, Vue, Prop, Emit } from "vue-property-decorator";
@Component
export default class SideBar extends Vue {
    @Prop()
    public value!: boolean;
    @Emit()
    public input(value: boolean) {
    }
    public set drawer(value: boolean) {
        this.input(value);
    }
    public get drawer() {
        return this.value;
    }
    public get items() {
        return [
            {
                icon: 'mdi-home',
                title: 'Overview',
                link: '/',
            },
            {
                icon: 'mdi-table',
                title: 'Table',
                link: '/table',
            },
            {
                icon: 'mdi-bulletin-board',
                title: 'Kanban',
                link: '/kanban',
            },
            {
                icon: 'mdi-chart-timeline',
                title: 'Ganttchart',
                link: '/ganttchart',
            },
            {
                icon: 'mdi-calendar',
                title: 'Calendar',
                link: '/calendar',
            },
        ]
    }
    public get projects() {
        return ["Unnamed"]
    }
}
